@keyframes dash-line-move-data-v-5b63246b {
  0%,
  100% {
    stroke-dasharray: 100 100;
    stroke-dashoffset: -100;
    stroke-width: 1px;
    fill: hsla(0, 0%, 100%, 0.25);
